<template>
	<cost-success
		v-if="successfulSubmit"
		:success-msg="successInfo.msg"
		:cost-date="successInfo.costDate"
		:cost-fee="successInfo.costFee"
		:payment-method="successInfo.paymentMethod"
		:receipt-number="successInfo.receiptNumber"
		:on-done="onDone"
		:file-download-link="successInfo.download_link"
		:cost-title="successInfo.costTitle"
	/>
	<!-- page -->
	<div v-else class="d-flex flex-column justify-content-start float-content">
		<b-alert v-model="error" class="p-1" variant="danger">
			مشکلی در اتصال به اینترنت یا سرور پیش آمده است
		</b-alert>
		<div class="text-center mb-2">
			<div class="d-flex align-items-center justify-content-center mb-1">
				<b-avatar class="mr-1" size="40" variant="light-success">
					<book-icon :style="{ color: 'var(--success)' }" size="30" />
				</b-avatar>
				<h4 class="m-0">ویرایش هزینه</h4>
			</div>
			<span>
				در این بخش می‌‌توانید مبلغ هزینه انجام شده را ویرایش کنید.
			</span>
		</div>
  <b-overlay
      variant="white"
      :show="showLoading"
      spinner-variant="primary"
      blur="0"
      opacity=".75"
      rounded="sm"
    >
		<b-card class="py-1 mb-5">
			<b-form>
				<b-form-group label-for="cost-date" label="تاریخ هزینه">
					<input type="text" class="custom-input"/>
					<date-picker color="#7468F0" auto-submit v-model="expenseDate" custom-input=".custom-input"/>
				</b-form-group>
				<b-form-group label-for="cost-fee" label="مبلغ هزینه">
					<b-form-input id="cost-fee" v-model="newCost.expense_fee" style="text-align:left; direction: ltr;" type="text" required @input="onFocus"/>
				</b-form-group>
				<b-form-group label-for="cost-title" label="عنوان هزینه">
					<b-form-input
						id="cost-title"
						v-model="newCost.expense_name"
						type="text"
						required
					/>
				</b-form-group>
				<b-form-group label-for="cost-heading" label="سرفصل هزینه">
					<custom-vue-select
						id="cost-heading"
                        v-model="expense_category"
						type="text"
						:options="costHeadingoptions"
						based-on="value"
								target-key="key"
					>
						<template #footer>
							<b-button block variant="outline-primary" class="without-outline mt-1">
								<add-icon />
								ثبت عنوان دلخواه
							</b-button>
						</template>
					</custom-vue-select>
				</b-form-group>
				<b-form-group label-for="payment-method" label="نحوه پرداخت">
					<custom-vue-select
						v-model="payment"
						:options="receiptTypeData"
						based-on="value"
						target-key="key"
					></custom-vue-select>
				</b-form-group>
				<b-form-group label-for="receipt-number" label="شماره رسید">
					<b-form-input
						id="receipt-number"
						v-model="newCost.receipt_number"
						type="text"
						required
					/>
				</b-form-group>
			</b-form>

		<div class="mt-3">
			<b-button v-if="!pending" block variant="primary" @click="submitForm">
				<!-- <img
					:src="require('@/assets/icons/add_icon.svg')"
					alt="add"
					draggable="false"
					width="24"
					class="add-icon"
				/> -->
				ویرایش هزینه
			</b-button>
             <b-button
            v-if="pending"
            block
            type="submit"
            variant="primary"
          >
            <b-spinner
              small
              type="grow"
            />

          </b-button>
		</div>
         </b-card>
  </b-overlay>
	</div>

</template>

<script>
import {
	BButton,
	BCard,
	BFormGroup,
	BForm,
	BFormInput,
	BAvatar,
	BAlert,
	BCol,
	BFormRow,
    BOverlay,
    BSpinner
} from "bootstrap-vue";
import { v4 as uuidv4 } from "uuid";
import CustomVueSelect from "@core/components/custom-vue-select/CustomVueSelect.vue";
import BookIcon from "@/@core/components/svg-icons/BookIcon.vue";
import AddIcon from "@/@core/components/svg-icons/AddIcon.vue";
import managerService from "@/api/managerService";
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import CostSuccess from "./CostSuccess.vue";
import VuePersianDatetimePicker from "vue-persian-datetime-picker";
import { separator } from "@core/helper";

export default {
	components: {
		datePicker: VuePersianDatetimePicker,
		CostSuccess,
		BButton,
		BCard,
		BFormGroup,
		BForm,
		BFormInput,
		BAvatar,
		BAlert,
		CustomVueSelect,
		BCol,
		BFormRow,
		BookIcon,
		AddIcon,
        BOverlay,
        BSpinner
	},
	data() {
		return {
			expenseDate: '',
			token: '',
      showLoading: false,
       pending: false,
      		expense_category: "",
			payment: {targetValue: "", value: {key: '', value: ''}},
			newCost: {
				expense_fee: "",
				expense_name: "",
                expense_category_id: "",
				receipt_type_id: "",
				receipt_number: "",
				expense_date: "",
                expense_id: ""
			},
			costHeadingoptions: [],
			successInfo: {
				msg: "",
				costDate: "",
				costFee: "",
				paymentMethod: "",
				receiptNumber: "",
				download_link: "",
				costTitle: ""
			},
			error: false,
			successfulSubmit: false,
			receiptTypeData: []
		};
	},

	async created() {
        const expense_date = this.$route.query.locale_expense_date;
    this.token = localStorage.getItem('token')
		try {
			this.loading = true;
      this.showLoading = true
			const { data: expenseFilter } = await managerService.expenseName()
			// console.log(expenseFilter.list)
			this.costHeadingoptions = Object.entries(expenseFilter.list).map(item => ({ key: item[0], value: item[1] }))
// eslint-disable-next-line prefer-destructuring
const selectedCat = this.costHeadingoptions.find(e => e.key == this.$route.query.expense_category_id);
this.expense_category = { value: selectedCat.value, targetValue: selectedCat.key };
			const { data: receiptType } = await managerService.receiptType()
      this.receiptTypeData = Object.entries(receiptType.list).map((item) => ({
        key: item[0],
        value: item[1],
      }));

      this.loading = false;
      this.showLoading = false
    } catch (err) {
      console.log(err);
    }
	},
     mounted() {
        this.newCost.expense_id = this.$route.query.id
        this.expenseDate = this.$route.query.locale_expense_date
        this.newCost.expense_name = this.$route.query.expense_name
			 this.payment.targetValue = this.$route.query.receipt_type_id
			 this.payment.value = {value: this.$route.query.receipt_type, key:this.$route.query.receipt_type_id}
			 // this.newCost.receipt_type_id = this.$route.query.receipt_type
			 this.newCost.receipt_number = this.$route.query.receipt_number
        this.newCost.expense_fee = separator(this.$route.query.expense_fee)
        // this.newCost.expense_category_id = this.costHeadingoptions[0];

        this.newCost.expense_date = this.$route.query.locale_expense_date
    },
	methods: {
		onFocus() {
				this.newCost.expense_fee = separator(this.newCost.expense_fee)
		},
		getUniqueString() {
			return uuidv4();
		},
		async submitForm() {

			try {
				this.newCost.expense_category_id = this.expense_category.targetValue
				this.newCost.expense_date = this.expenseDate
				console.log(this.payment)
				if (this.payment.targetValue) {
          this.newCost.receipt_type_id = this.payment.targetValue;
        }
//   this.pending = true
				const res = await managerService.expenseUpdate(this.newCost)
				if (res.data.status === 'success') {
					this.successInfo.msg = res.data.message
					this.successInfo.costFee = res.data.expense_fee
					this.successInfo.receiptNumber = res.data.receipt_number
					this.successInfo.paymentMethod = res.data.receipt_type
					this.successInfo.costDate = res.data.expense_date
					this.successInfo.download_link = res.data.download_link
					this.successInfo.costTitle = res.data.expense_name
					this.successfulSubmit = true;
                    //   this.pending = false

				}
				// console.log(this.newCost);
			} catch (err) {
				console.log(err)
			}
// 			{
//     "expense_date": "1401/03/23",
//     "expense_fee": 1350000,
//     "expense_title": "عنوان هزینه",
//     "expense_category_id": 12,
//     "receipt_type_id": 34,
//     "receipt_number": "3659841256"
// }
		},
		onDone() {
			this.$router.push({ name: "costs" });
		},
	},
};
</script>
